<template>
	<div class="container">
		<quillEditor
			@onEditorSuccess='onEditorSuccess'
			@onQuillBack='onQuillBack'
			@onEditorReady='onEditorReady' />
	</div>
</template>

<script>
import quillEditor from '@/components/Other/QuillEditor.vue'
import { upload } from '@/common/js/utils'
export default {
	data () {
		return {
		}
	},
	methods: {
		async onEditorSuccess(e) {
			for(let item of e.ops) {
				if(item.insert.image) {		// 
					if(item.insert.image.indexOf('data:image') >= 0) {		// 
						let res = await upload({ base64: item.insert.image, scale: 1 }, 'test')
						item.insert.image = `${config.IMG_URL}${res.url}`		// url
					}
				}
			}
			// `e`
			console.log(e)
		},
		// 
		onEditorReady(e) {
			e.setContents({"ops":[{"insert":"jsdafls\nasdljf\n"},{"attributes":{"color":"#fab410"},"insert":"ajsfdsa"},{"insert":"\n"},{"attributes":{"list":"ordered"},"insert":"\n"},{"insert":""},{"attributes":{"list":"ordered"},"insert":"\n"},{"insert":""},{"attributes":{"list":"ordered"},"insert":"\n"},{"insert":"\n\n"}]})
		},
		// equill
		onQuillBack(e) {
			console.log(e)
			e.deleteText(0, e.getLength())
		}
	},
	components: {
		quillEditor
	}
}
</script>

<style lang="scss">
</style>


